import React, {useState} from 'react';
import { Text, View, StyleSheet, FlatList, TouchableOpacity, TouchableWithoutFeedback, Keyboard } from 'react-native';
import Modal from 'modal-react-native-web';
import {globalStyles} from '../styles/global'
import {MaterialIcons} from '@expo/vector-icons'
import Card from '../shared/card'
import ReviewForm from './reviewForm'
import DeleteButton from '../shared/deleteButton';

export default function Home({navigation}) {
    const [reviews, setReviews] = useState([
        { title: 'Zelda, Breath of Fresh Air', rating: 5, body: 'lorem ipsum', key: 0},
        { title: 'Gotta Catch Them All (again)', rating: 4, body: 'lorem ipsum', key: 1 },
        { title: 'Not So "Final" Fantasy', rating: 3, body: 'lorem ipsum', key: 2 },
    ]);
    const [modalOpen, setModalOpen] = useState(false)
    const [key, setKeys] =useState(3)

    const pressHandler = () => {
        navigation.navigate('ReviewDetails')
    }
    
    const addReview = (review) => {
        setKeys(key+1)
        console.log(key)
        setReviews((currentReviews) =>{
            return [...currentReviews, review]
        })
        setModalOpen(false)
    }

    const deleteReview = (key) =>{
        setReviews((prevReviews)=>{
          return prevReviews.filter(review=> review.key != key)
        })
    }

    return (
        <View style={globalStyles.container}>
            <Modal visible={modalOpen} animationType='slide'>
                <TouchableWithoutFeedback  onPress={Keyboard.dismiss}>
                    <View style={localStyles.modalContent}>
                        <MaterialIcons 
                            name='close' 
                            size={24} 
                            style={{...localStyles.modalToggle, marginTop:30}}
                            onPress={()=>setModalOpen(false)}
                        />
                        <ReviewForm addReview={addReview} />
                    </View>
                </TouchableWithoutFeedback>
            </Modal>
            <MaterialIcons 
                name='add' 
                size={24} 
                style={localStyles.modalToggle}
                onPress={()=>setModalOpen(true)}
            />
            <FlatList 
                data={reviews}
                keyExtractor={(item) => item.key}
                renderItem={({item})=>(
                    <View style={localStyles.cardBox}>
                        <TouchableOpacity onPress={()=>navigation.navigate('ReviewDetails', item)}>
                            <Card>
                                <Text style={globalStyles.titleText}>{item.title} 
                                </Text>
                                <DeleteButton item={item} deleteReview={deleteReview}/>
                            </Card>
                        </TouchableOpacity>
                        
                    </View>
                )}
            />
        </View>
    );
}
const localStyles = StyleSheet.create({
    modalContent:{
        flex:1,
        margin:40
    },

    modalToggle:{
        marginBottom:10,
        borderWidth:1,
        borderColor:'#f2f2f2',
        padding:10,
        borderRadius:10,
        alignSelf:'center' 
    }
        
})