import React from 'react';
import { StyleSheet, Text, TouchableOpacity, View } from 'react-native'
import {MaterialIcons} from '@expo/vector-icons'

function DeleteButton({ item, deleteReview }) {

    return (
        <TouchableOpacity onPress={()=>deleteReview(item.key)} style={styles.deleteIcon}>
            <View>
                <MaterialIcons name='delete' size={15} color='#333'/>
            </View>
        </TouchableOpacity>
    );
}

const styles = StyleSheet.create({
    deleteIcon:{
        width:35,
        borderWidth:1,
        borderColor:'#f2f2f2',
        padding:10,
        borderRadius:10,
        alignSelf:'flex-end'
    }
})
export default DeleteButton;