import React from 'react'
import {Image, StyleSheet, Text, View, ImageBackground, Dimensions} from 'react-native'
import {MaterialIcons} from '@expo/vector-icons'


function Header({navigation, title}) {
    const pressHandler = () => {
        navigation.openDrawer(); 
    }

    return (
        <ImageBackground style={styles.header} source={require('../assets/game_bg.png')}>
            <MaterialIcons name='menu' size={28} onPress={pressHandler} style={styles.icon} />
            <View style={styles.headerTitle}>
                <Image source={require('../assets/heart_logo.png')} style={styles.headerImg} />
                <Text style={styles.headerText}>{title}</Text>
            </View>
        </ImageBackground>
    );
}

const styles = StyleSheet.create({
    header:{
        width:Dimensions.get('screen').width,
        height:'100%',
        flexDirection:'row',
        alignItems:'center',
        justifyContent:'center'
    },
    headerTitle:{
        flexDirection: 'row'
    },
    headerText:{
        fontWeight:'bold',
        fontSize:20,
        letterSpacing:1
    },

    icon:{
        position:'absolute',
        left:16,
    },
    headerImg:{
        width:26,
        height:26,
        marginHorizontal:10
    }
})


export default Header;
